<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                placeholder="监测对象、备案号、产权备案号、特种设备过滤"
                @callFunction="callFunction"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column prop="siteshortname" label="监测对象">
                </el-table-column>
                <el-table-column
                    prop="recordno"
                    label="备案号"
                ></el-table-column>
                <el-table-column prop="rightno" label="产权备案号">
                </el-table-column>
                <el-table-column prop="setype" label="设备类型">
                    <nobr slot-scope="scope">
                        {{ scope.row.setype ? msetype[scope.row.setype] : "" }}
                    </nobr>
                </el-table-column>
                <el-table-column prop="sename" label="特种设备">
                </el-table-column>
                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
                width="70%"
                top="5vh"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-row>
                        <el-col :span="6">
                            <el-form-item label="监测对象分组" prop="GROUPID">
                                <el-select
                                    v-model="editForm.GROUPID"
                                    filterable
                                    placeholder="请选择"
                                    @change="groupChange"
                                >
                                    <el-option
                                        v-for="item in groups"
                                        :key="item.GROUPID"
                                        :label="item.groupshortname"
                                        :value="item.GROUPID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="监测对象" prop="SITEID">
                                <el-select
                                    v-model="editForm.SITEID"
                                    filterable
                                    placeholder="请选择"
                                    @change="siteChange($event, 'edit')"
                                >
                                    <el-option
                                        v-for="item in selectSites"
                                        :key="item.SITEID"
                                        :label="item.siteshortname"
                                        :value="item.SITEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="特种设备类型" prop="setype">
                                <el-select
                                    v-model="editForm.setype"
                                    placeholder="请选择"
                                    @change="typeChange"
                                >
                                    <el-option
                                        v-for="(value, key) in msetype"
                                        :key="key"
                                        :label="value"
                                        :value="Number(key)"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="机械类型" prop="machinerytype">
                                <el-select
                                    v-model="editForm.machinerytype"
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="(value, key) in mmachinerytype"
                                        :key="key"
                                        :label="value"
                                        :value="Number(key)"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="特种设备" prop="SEID">
                                <el-select
                                    v-model="editForm.SEID"
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="it in selecteqps"
                                        :key="it.SEID"
                                        :label="it.sename"
                                        :value="it.SEID"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="备案号" prop="recordno">
                                <el-input
                                    v-model="editForm.recordno"
                                    auto-complete="off"
                                    maxlength="10"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="产权备案号" prop="rightno">
                                <el-input
                                    v-model="editForm.rightno"
                                    auto-complete="off"
                                    maxlength="20"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="是否产权注销" prop="brightdel">
                                <el-select
                                    v-model="editForm.brightdel"
                                    placeholder="请选择"
                                >
                                    <el-option
                                        label="是"
                                        :value="1"
                                    ></el-option>
                                    <el-option
                                        label="否"
                                        :value="0"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item
                                label="产权设备单位"
                                prop="rightcompany"
                            >
                                <el-input
                                    v-model="editForm.rightcompany"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="告知日期" prop="notifydate">
                                <el-date-picker
                                    v-model="editForm.notifydate"
                                    type="date"
                                    placeholder="选择日期"
                                >
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="安装单位" prop="installer">
                                <el-input
                                    v-model="editForm.installer"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="安装日期" prop="installdate">
                                <el-date-picker
                                    v-model="editForm.installdate"
                                    type="date"
                                    placeholder="选择日期"
                                >
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="检测合格情况" prop="checkinfo">
                                <el-input
                                    v-model="editForm.checkinfo"
                                    auto-complete="off"
                                    maxlength="20"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="检测合格日期" prop="checkdate">
                                <el-date-picker
                                    v-model="editForm.checkdate"
                                    type="date"
                                    placeholder="选择日期"
                                >
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="使用登记情况" prop="useinfo">
                                <el-input
                                    v-model="editForm.useinfo"
                                    auto-complete="off"
                                    maxlength="20"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="使用登记日期" prop="usedate">
                                <el-date-picker
                                    v-model="editForm.usedate"
                                    type="date"
                                    placeholder="选择日期"
                                >
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="拆卸单位" prop="uninstaller">
                                <el-input
                                    v-model="editForm.uninstaller"
                                    auto-complete="off"
                                    maxlength="50"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item
                                label="拆卸告知日期"
                                prop="uninstalldate"
                            >
                                <el-date-picker
                                    v-model="editForm.uninstalldate"
                                    type="date"
                                    placeholder="选择日期"
                                >
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item
                                prop="machinerymodel"
                                class="green-require"
                            >
                                <label slot="label" class="item-label"
                                    >机械型号<font>(示范片区必填)</font></label
                                >
                                <el-input
                                    maxlength="20"
                                    v-model="editForm.machinerymodel"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item
                                prop="machinerycheckstate"
                                class="green-require"
                            >
                                <label slot="label" class="item-label"
                                    >检测状态<font>(示范片区必填)</font></label
                                >
                                <el-select
                                    v-model="editForm.machinerycheckstate"
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="(
                                            value, key
                                        ) in mmachinerycheckstate"
                                        :key="key"
                                        :label="value"
                                        :value="Number(key)"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item
                                prop="checkstate"
                                class="green-require"
                            >
                                <label slot="label" class="item-label"
                                    >机械状态<font>(示范片区必填)</font></label
                                >
                                <el-select
                                    v-model="editForm.checkstate"
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="(value, key) in mcheckstate"
                                        :key="key"
                                        :label="value"
                                        :value="Number(key)"
                                    ></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item prop="oem" class="green-require">
                                <label slot="label" class="item-label"
                                    >生产厂家<font>(示范片区必填)</font></label
                                >
                                <el-input
                                    maxlength="50"
                                    v-model="editForm.oem"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item
                                prop="leavethefactoryno"
                                class="green-require"
                            >
                                <label slot="label" class="item-label"
                                    >出厂编号<font>(示范片区必填)</font></label
                                >
                                <el-input
                                    maxlength="50"
                                    v-model="editForm.leavethefactoryno"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item
                                prop="rightcompanyid"
                                class="green-require"
                            >
                                <label slot="label" class="item-label"
                                    >产权单位代码<font
                                        >(示范片区必填)</font
                                    ></label
                                >
                                <el-input
                                    maxlength="18"
                                    v-model="editForm.rightcompanyid"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item
                                prop="userentcode"
                                class="green-require"
                            >
                                <label slot="label" class="item-label"
                                    >使用单位代码<font
                                        >(示范片区必填)</font
                                    ></label
                                >
                                <el-input
                                    maxlength="18"
                                    v-model="editForm.userentcode"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>

                        <el-col :span="6">
                            <el-form-item
                                prop="userentname"
                                class="green-require"
                            >
                                <label slot="label" class="item-label"
                                    >使用单位-名称<font
                                        >(示范片区必填)</font
                                    ></label
                                >
                                <el-input
                                    maxlength="50"
                                    v-model="editForm.userentname"
                                    auto-complete="off"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item prop=""
                                ><label slot="label" class="item-label"
                                    >上传图片<font style="color: #bebebe"
                                        >jpg/png/pdf</font
                                    ></label
                                >
                                <el-upload
                                    class="upload-demo"
                                    action="1"
                                    :on-remove="handleRemove"
                                    :on-change="fileChanged"
                                    accept=".jpg,.png,.pdf"
                                    :file-list="fileList"
                                    :http-request="fileUpload"
                                    list-type="picture-card"
                                >
                                    <i slot="default" class="el-icon-plus"></i>
                                    <div slot="file" slot-scope="{ file }">
                                        <el-image
                                            class="
                                                el-upload-list__item-thumbnail
                                            "
                                            :src="file.url"
                                            fit="cover"
                                        ></el-image>
                                        <span
                                            class="el-upload-list__item-actions"
                                        >
                                            <span
                                                class="
                                                    el-upload-list__item-preview
                                                "
                                                @click="handleOpenFile(file)"
                                            >
                                                <i class="el-icon-zoom-in"></i>
                                            </span>
                                            <span
                                                class="
                                                    el-upload-list__item-delete
                                                "
                                                @click="handleRemove(file)"
                                            >
                                                <i class="el-icon-delete"></i>
                                            </span>
                                        </span>
                                    </div>
                                </el-upload>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getspecialrecordpagelist,
    getspecialrecordGroups,
    removespecialrecord,
    editspecialrecord,
    addspecialrecord,
    postfile,
    geteqp,
    geteqpproof,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import { msetype } from "../../assets/dic/dic";
export default {
    components: { leftgroup, Toolbar },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            // addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                setype: [
                    {
                        required: true,
                        message: "特种设备类型",
                        trigger: "blur",
                    },
                ],
                recordno: [
                    {
                        required: true,
                        message: "请填写备案号",
                        trigger: "blur",
                    },
                ],
                rightno: [
                    {
                        required: true,
                        message: "请填写产权备案号",
                        trigger: "blur",
                    },
                ],
                machinerytype: [
                    {
                        required: true,
                        message: "请填写机械类型",
                        trigger: "blur",
                    },
                ],
                brightdel: [
                    {
                        required: true,
                        message: "请选择是否产权注销",
                        trigger: "blur",
                    },
                ],
                rightcompany: [
                    {
                        required: true,
                        message: "请填写产权设备单位",
                        trigger: "blur",
                    },
                ],
                installer: [
                    {
                        required: true,
                        message: "请填写安装单位",
                        trigger: "blur",
                    },
                ],
                installdate: [
                    {
                        required: true,
                        message: "请选择安装日期",
                        trigger: "blur",
                    },
                ],
            },
            mmachinerytype: {},
            mmachinerytypeall: {
                0: "塔式起重机",
                1: "施工升降机",
                2: "货运施工升降机",
                3: "桥式起重机",
                4: "门式起重机",
            },
            mmachinerycheckstate: {
                0: "未检测",
                4: "非我所检测",
                5: "检测中",
                6: "检测合格",
                7: "检测不合格",
                17: "复检中",
                18: "复检合格",
                19: "复检不合格",
            },
            mcheckstate: {
                0: "未安装告知",
                1: "安装告知审核中",
                2: "安装告知审核通过",
                3: "安装告知审核不通过",
                6: "检测合格",
                8: "办理使用登记审核中",
                9: "办理使用登记未通过",
                10: "办理使用登记通过",
                11: "拆卸告知审核中",
                12: "拆卸告知审核通过",
                13: "拆卸告知审核不通过",
                14: "使用登记注销审核中",
                15: "使用登记注销审核通过",
                16: "使用登记注销审核不通过",
            },
            editForm: {},
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
            fileList: [],
            msetype: msetype,
            selecteqps: [],
            fileDelList: [],
            isEdit: false,
        };
    },
    methods: {
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getPageList();
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                groupid: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getspecialrecordpagelist(paras).then((res) => {
                if (res.success) {
                    var temp = res.data.data || [];
                    this.builds = temp.map((t) => {
                        t.SEID = t.SEID == 0 ? "" : t.SEID;
                        return t;
                    });
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        getGroupCounts() {
            getspecialrecordGroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        groupChange(val) {
            this.getGroupSites(val);
            // this.$set(this.addForm, "SITEID", "");
            this.$set(this.editForm, "SITEID", "");
            this.selectSites = this.groupSites;
        },
        siteChange(val, type) {
            // ? this.$set(this.addForm, "SEID", undefined)
            this.$set(this.editForm, "SEID", undefined);
            let paras = {
                GROUPID: this.editForm.GROUPID,
                SITEID: this.editForm.SITEID,
                setype: this.editForm.setype,
            };

            geteqp(paras).then((res) => {
                this.selecteqps = res.data;
            });
        },
        siteInit(row) {
            let paras = {
                GROUPID: row.GROUPID,
                SITEID: row.SITEID,
                setype: row.setype,
            };

            geteqp(paras).then((res) => {
                this.selecteqps = res.data;
            });
        },
        typeChange(val) {
            this.$set(this.editForm, "SEID", undefined);
            this.mmachinerytype = {};
            if (val == "1") {
                //塔吊
                this.mmachinerytype["0"] = this.mmachinerytypeall["0"];
                this.mmachinerytype["3"] = this.mmachinerytypeall["3"];
                this.mmachinerytype["4"] = this.mmachinerytypeall["4"];
            } else {
                //升降机
                this.mmachinerytype["1"] = this.mmachinerytypeall["1"];
                this.mmachinerytype["2"] = this.mmachinerytypeall["2"];
            }
            let paras = {
                GROUPID: this.editForm.GROUPID,
                SITEID: this.editForm.SITEID,
                setype: this.editForm.setype,
            };
            geteqp(paras).then((res) => {
                this.selecteqps = res.data;
            });
        },
        //文件上传
        fileUpload(param) {
            let fileObj = param.file; // 文件对象
            let form = new FormData(); // FormData 对象
            form.append("formFiles", fileObj);
            postfile(form)
                .then((res) => {
                    this.fileList[this.fileList.length - 1].filename = res.data;
                })
                .catch((err) => {});
        },
        fileChanged(file, fileList) {
            this.fileList = fileList;
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.SERID };
                    removespecialrecord(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getPageList();
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            this.isEdit = true;
            this.fileList = [];
            this.fileDelList = [];
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.siteInit(row);
            if (row.notifydate < "1900-01-01") {
                row.notifydate = "";
            }
            if (row.checkdate < "1900-01-01") {
                row.checkdate = "";
            }
            if (row.uninstalldate < "1900-01-01") {
                row.uninstalldate = "";
            }
            if (row.usedate < "1900-01-01") {
                row.usedate = "";
            }
            this.editFormVisible = true;
            this.groupChange(row.GROUPID);
            this.editForm = Object.assign({}, row);
            this.mmachinerytype = {};

            if (this.editForm.setype == "1") {
                //塔吊
                this.mmachinerytype["0"] = this.mmachinerytypeall["0"];
                this.mmachinerytype["3"] = this.mmachinerytypeall["3"];
                this.mmachinerytype["4"] = this.mmachinerytypeall["4"];
            } else {
                //升降机
                this.mmachinerytype["1"] = this.mmachinerytypeall["1"];
                this.mmachinerytype["2"] = this.mmachinerytypeall["2"];
            }

            let paras = {
                GROUPID: row.GROUPID,
                SITEID: row.SITEID,
                SERID: row.SERID,
            };
            geteqpproof(paras).then((res) => {
                this.fileList = res.data;
            });
        },
        //显示新增界面
        handleAdd() {
            this.isEdit = false;
            this.fileList = [];
            this.editForm = {};
            this.editFormVisible = true;
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    if (this.isEdit) {
                        this.editLoading = true;
                        this.editForm.SEID =
                            this.editForm.SEID == "" ? 0 : this.editForm.SEID;
                        let para = Object.assign(
                            {
                                fileList: this.fileList,
                                fileDelList: this.fileDelList,
                            },
                            this.editForm
                        );

                        editspecialrecord(para).then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: "修改成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.getPageList(this.page);
                            } else {
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }

                            this.editLoading = false;
                        });
                    } else {
                        this.editLoading = true;
                        let para = Object.assign(
                            { fileList: this.fileList },
                            this.editForm
                        );
                        addspecialrecord(para).then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: "新增成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.getPageList(this.page);
                                this.getGroupCounts();
                            } else {
                                this.$message({
                                    message: res.msg,
                                    type: "error",
                                });
                            }
                            this.editLoading = false;
                        });
                    }

                    //});
                }
            });
        },
        handleRemove(file, fileList) {
            for (let i = 0; i < this.fileList.length; i++) {
                if (file.uid == this.fileList[i].uid) {
                    this.fileList.splice(i, 1); //删除指定元素
                    break;
                }
            }
            // this.fileList = fileList;
            this.fileDelList.push(file.id);
        },
        handleOpenFile(file) {
            window.open(file.url);
        },
    },
    mounted() {
        this.getPageList();
        this.getGroupCounts();
        this.getGroups();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
    width: 100%;
}
.upload-demo /deep/ .el-upload-list__item {
    transition: none !important;
}
.upload-demo /deep/ .el-upload-list li div {
    height: 100%;
}
.green-require /deep/ .el-form-item__label {
    line-height: 20px !important;
}
.item-label font {
    color: red;
    display: block;
}
.upload-demo /deep/ .el-upload-list--picture-card .el-upload-list__item,
.upload-demo /deep/ .el-upload--picture-card {
    height: 100px;
    line-height: 106px;
    width: 100px;
}
</style>


